.movies{
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.movies .tabs{
  min-height : calc(100vh - 357px);
}

.movies .asideNav{
  display : -ms-flexbox;
  display : flex;
  -ms-flex-wrap : wrap;
      flex-wrap : wrap;
  background-color : white;
}

.movies .asideNav a{
  padding : 30px;
  color : black;
  font-weight : bold;
}

.movies .tabs{
  padding : 30px;
  height : auto !important;
  background-color : #eee !important;
  white-space : normal !important;
}

.movies .tabs h1{
  margin-top : 0;
}

.movies .asideNav a.active{
  color : #ec4531;
  border-bottom : 1px solid #ec4531;
}

@media screen and (min-width : 732px) and (max-width : 960px){
  .movies .tabs .list > div img{
    max-width : 256px;
    display : block;
    margin : 0 auto;
  }
}

@media screen and (min-width : 320px) and (max-width : 964px){
  .movies .tabs .list > div{
    min-width : 260px;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .movies .tabs .list > div img{
    width : 100%;
    margin-bottom : 20px;
  }
}

@media screen and (min-width : 320px) and (max-width : 605px){
  .movies .tabs .list{
    -ms-flex-pack: center;
        justify-content: center;
  }
}


