.footer{
	padding : 30px;
	background-image : url('../img/slider-bg.jpg');
	background-size : cover;
	background-repeat : none;
	background-position : top left;
	display : -ms-flexbox;
	display : flex;
	color : white;
	-ms-flex-wrap : wrap;
	    flex-wrap : wrap;
}

.footer a{
	color : white;
	padding : 10px 0;
}

.footer div{
	display : -ms-flexbox;
	display : flex;
	-ms-flex-direction : column;
	    flex-direction : column;
	padding : 0 15px;
	font-size : 13px;
	-ms-flex-positive : 1;
	    flex-grow : 1;
}

.footer .message{
	-ms-flex-positive : 1;
	    flex-grow : 1;
	max-width:100%;
}

.footer .message textarea{
	height : 100px;
	width : 450px;
	max-width : 100%;
	border-radius : 15px;
	background-color : transparent;
	color : white;
}


